h4{
    font-size: 25px;
    border-bottom: 1px solid #ccc;
    padding-bottom: 4px;
    margin-bottom: 20px;
    position: relative;
    text-align: center;
}

.center {
    margin: auto;
    width: 600px;
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 40px;
}

.group{
    width: 80%;
    max-width: 800px;
    margin: 40px auto;
    padding: 20px;
    position: relative;
    overflow: hidden;
}

.group.connectionState{
    margin: 10px auto 0;
    padding: 0 20px;
}

input{
    border-radius: 4px;
    border: 1px solid #ccc;
}

input:focus{
    border-color: #7185ec;
}

.input-group input{
    width: 100%;
}

label{
    font-size: 17px;
    font-style: italic;
}

.submit{
    border: none;
    background: #7185ec;
    color: #fff;
    font-weight: 500;
    border-radius: 4px;
    cursor: pointer;
    text-align: center;
    cursor: pointer;
    font-weight: bold;
    -webkit-box-shadow: 1px 1px 3px 0px rgba(0, 0, 0, 0.2);
            box-shadow: 1px 1px 3px 0px rgba(0, 0, 0, 0.2);
    width: 20%;
    float: left;
    position: relative;
    margin-right: 4%;
    margin-bottom: 10px;
}

img.avatar {
    width: 40%;
    border-radius: 50%;
}

.imgcontainer {
    text-align: center;
    margin: 24px 0 12px 0;
}


